<template>
  <v-container>
    <v-card>
      <v-toolbar color="blue" light>
        <v-app-bar-nav-icon></v-app-bar-nav-icon>
        <v-toolbar-title class="white--text">Avaya Справочник</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>mdi-view-module</v-icon>
        </v-btn>
      </v-toolbar>
  <v-spacer></v-spacer>
      <v-spacer></v-spacer>
      <v-data-table
              dense
              :headers="headers"
              :items="tableData"
              :items-per-page="15"
              :search="search"
              :fixed-header="true"
              :loading="isLoading"
              class="elevation-1"
      >
       <template v-if="1" v-slot:progress>
        <v-progress-linear color="blue" :height="10" indeterminate></v-progress-linear>
      </template>
        </v-data-table>
    </v-card>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-text>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-menu
                      v-model="menuDateFrom"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                          v-model="dateFrom"
                          label="Date from"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker v-model="dateFrom" @input="menuDateFrom = false"></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" sm="6" md="4"><v-menu
                     ref="menuTF"
                     v-model="menuTimeFrom"
                      :close-on-content-click="false"
                      :nudge-right="40"
                     :return-value.sync="timeFrom"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                          v-model="timeFrom"
                          label="time from"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker format="24hr" v-model="timeFrom"  @click:minute="$refs.menuTF.save(timeFrom)"></v-time-picker>
              </v-menu></v-col>
          </v-row>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-menu
                      v-model="menuDateTo"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                          v-model="dateTo"
                          label="Date to"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker v-model="dateTo" @input="menuDateTo = false"></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" sm="6" md="4"><v-menu
                    ref="menuTT"
                      v-model="menuTimeTo"
                      :close-on-content-click="false"
                      :nudge-right="40"
                    :return-value.sync="timeTo"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                          v-model="timeTo"
                          label="time from"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker format="24hr" v-model="timeTo" @click:minute="$refs.menuTT.save(timeTo)"></v-time-picker>
              </v-menu></v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="getSyslog">Submit</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
    import axios from 'axios'

    //let reqPath = 'http://10.50.2.193:8082'
    axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*'
    export default {
        name: "SyslogTableV",
        data() {
            return {
                searchSev: '',
                menuDateFrom: false,
                menuDateTo: false,
                menuTimeFrom: false,
                menuTimeTo: false,
                dataDownload: false,
                timeFrom: new Date((new Date()) - 10*60*1000).toLocaleTimeString().substr(0, 5),
                timeTo: new Date().toLocaleTimeString().substr(0, 5),
                dateFrom: new Date().toISOString().substr(0, 10),
                dateTo: new Date().toISOString().substr(0, 10),
                ip: '',
                head_part: '',
                search: '',
                dialog: false,
                datetime_start: '',
                datetime_end: '',
                slots: [
                    'pogress',
                ],
                headers: [
                    {text: 'Ext', align: 'center', value: 'ext', width: "150px"},
                    {text: 'Model', value: 'model'},
                    {text: 'Localizedname', value: 'localizedname'}
                ],
                tableData: []
            }
        },
        mounted() {
            this.getSyslog();
        },
        computed: {
            dateDisplay: function () {
                return this.dateFrom + ' ' + this.timeFrom + ' - ' + this.dateTo + ' ' + this.timeTo
            } ,
            isLoading () {
              return this.dataDownload
            },
        },
        methods: {
            getColor (Severity) {
                if (Severity < 4) return 'red'
                else if (Severity == 4 ) return 'orange'
                    else if (Severity == 7) return 'yellow'
                else return 'green'
            },
            getSyslog() {
                this.dialog = false
                this.dataDownload = true
                this.tableData=[]
                // eslint-disable-next-line no-console
                console.log(process.env)
                axios.post(process.env.VUE_APP_RECORD_REQ_URL, {
                    sdate: this.dateFrom + 'T' + this.timeFrom + ':00.000Z',
                    edate: this.dateTo + 'T' + this.timeTo + ':00.000Z',
                    ipaddr: this.ip,
                    header: this.header,
                    message: this.message,
                    severity: this.severity,
                    facility: this.facility
                })
                    .then(response => {

                        // eslint-disable-next-line no-console
                        console.log(response.data['liststation']);
                        if (response.data['liststation'] === null) {
                            this.tableData = []
                        }
                        else
                        {
                            this.tableData = response.data['liststation']
                        }
                        this.dataDownload = false
                    })
                    .catch(error => {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    })
            }
        }


    }
</script>

